<template>
  <div class="pc-container pb-4">
    <div class="pcoded-content">
      <div v-if="mostrarOpcionAdmin">
        <CardHomeAdmin />
      </div>
      <div v-else>
        <CardHomeUser />
      </div>
      <div class="row">
        <div class="col-xl-12 col-md-12">
          <div class="card shadow rounded">
            <div class="card-header border-0">
              <h6 class="text-secondary p-0 m-0">Últimos cinco expedientes registrados en el sistema</h6>
            </div>
            <div class="card-body mx-0 px-0 mt-0 pt-0">
              <div class="table-responsive ">
                <table class="table">
                  <thead>
                    <tr>
                      <th class="pl-4">N° de Expediente</th>
                      <th>Fecha de Inicio</th>
                      <th>Demandante</th>
                      <th>Demandado</th>
                      <th>Pretensión</th>
                      <th>Materia</th>
                      <th>Estado</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="expediente in expedientes" :key="expediente.exp_id">
                      <td class="pl-4">
                        <router-link :to="{ name: 'expediente/detalle', params: { id: expediente.exp_id } }">
                          {{ expediente.numero }}
                        </router-link>
                      </td>
                      <td>{{ expediente.fecha_inicio }}</td>
                      <td v-if="expediente.multiple == '0' && expediente.procesal[0].tipo_procesal == 'DEMANDANTE'
                        && expediente.procesal[0].tipo_persona == 'NATURAL'">{{
                        expediente.procesal[0].apellido_paterno + ' ' +
                        expediente.procesal[0].apellido_materno + ' ' + expediente.procesal[0].nombres }}
                      </td>
                      <td v-if="expediente.multiple == '0' && expediente.procesal[0].tipo_procesal == 'DEMANDANTE'
                        && expediente.procesal[0].tipo_persona == 'JURIDICA'"
                      >{{
                        expediente.procesal[0].razon_social }}</td>
                      <td v-if="expediente.multiple == '1' && expediente.procesal[0].tipo_procesal == 'DEMANDANTE'
                        && expediente.procesal[0].tipo_persona == 'JURIDICA'"
                      >Multiples Demandantes</td>
                      <td v-if="expediente.multiple == '1' && expediente.procesal[0].tipo_procesal == 'DEMANDANTE'
                        && expediente.procesal[0].tipo_persona == 'NATURAL'"
                      >Multiples Demandantes</td>
                      <td>UNPRG</td>
                      <td v-if="expediente.multiple == '0' && expediente.procesal[0].tipo_procesal == 'DEMANDADO'
                        && expediente.procesal[0].tipo_persona == 'NATURAL'">{{
                        expediente.procesal[0].apellido_paterno + ' ' +
                        expediente.procesal[0].apellido_materno + ' ' + expediente.procesal[0].nombres }}
                      </td>
                      <td v-if="expediente.multiple == '0' && expediente.procesal[0].tipo_procesal == 'DEMANDADO'
                        && expediente.procesal[0].tipo_persona == 'JURIDICA'"
                      >{{
                        expediente.procesal[0].razon_social }}</td>
                        <td v-if="expediente.multiple == '1' && expediente.procesal[0].tipo_procesal == 'DEMANDADO'
                        && expediente.procesal[0].tipo_persona == 'JURIDICA'"
                      >Multiples Demandantes</td>
                      <td v-if="expediente.multiple == '1' && expediente.procesal[0].tipo_procesal == 'DEMANDADO'
                        && expediente.procesal[0].tipo_persona == 'NATURAL'"
                      >Multiples Demandantes</td>
                      <td>{{ expediente.pretencion}}</td>
                      <td>{{ expediente.materia}}</td>
                      <td class="pr-5">
                        <span :class="badgeClass(expediente.estado_proceso)">
                          {{ expediente.estado_proceso }}
                        </span>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CardHomeUser from '../components/Card/CardHomeUser.vue';
import CardHomeAdmin from '../components/Card/CardHomeAdmin.vue';
import { badgeClass } from '@/utils/utils.js';
import '@/assets/css/fileStatus.css';
import feather from 'feather-icons';
export default {
  name: 'dashboard',
  components: {
    CardHomeUser,
    CardHomeAdmin
  },
  data() {
    return {
      expedientes: [],
      loader: false
    };
  },
  computed: {
    rol() {
      return this.$store.getters.get__user.usu_rol;
    },
    mostrarOpcionAdmin() {
      return this.rol === 'ADMIN';
    },
  },
  mounted() {
    this.cargarExpedientes();
    this.$nextTick(() => {
      feather.replace();
    });
  },
  methods: {
    async cargarExpedientes() {
      try {
        this.$showLoadingAlert();
        const response = await this.$postData('proceeding/take', this);
        this.expedientes = response.data;
        this.$closeLoadingAlert();
      } catch (error) {
        this.$handleApiError(error, 'leer');
      }
    },
    badgeClass
  },
}
</script>

<style scoped>
@import '../assets/css/dataTable.css';
</style>
